@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  background-color: #eee;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
}

.profile-card {
  text-align: center;
}

.profile-picture {
  border-radius: 100%;
  width: 150px;
  height: 150px;
}

.title {
  font-weight: normal;
  font-size: 20px;
  margin: 5px;
}

.about {
  margin-top: 40px;
}

main {
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
}

.filter {
  float: left;
  width: 20%;
  background-color: green;
  margin-right: 15px;
  margin-top: 15px;
}

@media only screen and (max-width: 768px) {
    .filter {
      display: none;
    }
}

.grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
  margin: 0px auto;
}

.cell {
  flex: 1 1 300px; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */

  min-width: 300px;
  max-width: 550px;
  min-height: 450px;
  margin: 15px;

  padding: 20px;

  background-color: white;
  border: 1px solid #ddd;

  position: relative;
}

.cell-date {
  font-weight: normal;
  padding: 20px 0px;
  position: absolute;
  bottom: 0;
}

@media only screen and (max-width: 768px) {
    .cell {
      min-width: 100%;
      max-width: 100%;
      margin: 10px 0px
    }
}

.cell:hover {
  box-shadow: 0px 2px 10px 0 rgba(0, 0, 0, 0.1), 0 0px 0px 0 rgba(0, 0, 0, 0.1);
}

body {
  background-color: #eee;
}

a {
  color: #4F90CD;
  text-decoration: none;
  font-weight: normal;
}

p {
  margin-top: 20px;
  margin-bottom: 20px;
}

.container-small {
  max-width: 700px;
  margin: 0px auto;
  padding: 40px 20px;
}

.container-medium {
  max-width: 1100px;
  margin: 0px auto;
  padding: 40px 20px;
}

